html,
body {
    margin: 0;
    font-family: Inter, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #292929;
    color: #fff;
    overflow-x: hidden;
}
#root {
    min-height: 100vh;
    display: flex;
}

@media screen and (max-width: 768px) {
    h1 {
        font-size: 3rem;
    }
}

.unsupported-network {
    width: 340px;
    max-width: 100%;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    margin: auto;
    padding: 1rem;
}

.error {
    width: 340px;
    max-width: 100%;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    margin: auto;
    padding: 1rem;
    border-radius: 1rem;
    background-color: #f00;
}

.landing,
.connect-wallet,
.mint-nft,
.member-page {
    flex-direction: column;
    width: 960px;
    max-width: calc(100% - 1rem);
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    margin: auto;
    align-content: center;
    display: flex;
    text-align: center;
}

.member-page {
    margin: 0px auto;
}

.member-page > div {
    display: flex;
    flex-direction: row;
    width: 100%;
    text-align: left;
    gap: 2rem;
}
.member-page > div > div {
    display: flex;
    flex-direction: column;
    width: 50%;
    gap: 1rem;
}
@media screen and (max-width: 768px) {
    .member-page > div {
        flex-direction: column;
    }
    .member-page > div > div {
        width: 100%;
    }
}

.member-page form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.member-page form > div {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.member-page form h5 {
    margin-top: 0;
    color: #003fee;
}

.member-page form .card > div {
    display: flex;
    gap: 1rem;
    justify-content: space-between;
}

.member-page form small {
    text-align: center;
}

.card {
    background-color: #fff;
    padding: 1rem;
    border-radius: 1rem;
    color: #000;
    box-shadow: 3.1px 6.2px 6.2px hsl(0deg 0% 0% / 0.4);
}

button {
    cursor: pointer;
    background-color: #000;
    color: #fff;
    border: none;
    font-weight: bold;
    font-family: inherit;
    padding: 1.2rem 2rem;
    text-transform: uppercase;
    border-radius: 3rem;
    font-size: 1.2rem;
}

button:hover {
    background-color: #121212;
}

button:focus {
    background-color: #222;
}

button:disabled {
    opacity: 0.8;
    pointer-events: none;
}
